.wrapper {
    overflow-x: auto;

    padding: 2rem 0;
    font-size: 1.2rem;
    &::-webkit-scrollbar {
        display: none;
    }
    & {
        -ms-overflow-style: none; /* IE and Edge */
        scrollbar-width: none; /* Firefox */
    }
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 13%);
    .unit-price,
    .quantity,
    .item-thumb,
    .total-price,
    .item-thumb-name,
    .actions {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 1rem;
    }
    .unit-price,
    .total-price {
        min-width: 80px;
    }
    table {
        width: 100%;
        tbody {
            tr {
                &:last-child {
                    background-color: var(--gray);
                    td {
                        .checkbox {
                            width: 80px;
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                        }
                        .total-price-all-item {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                        }
                    }   
                }
                th {
                    padding: 1rem;
                    background-color: var(--gray);
                }
                td {
                    padding: 1rem 2rem;
                    .item-thumb-name {
                        width: 150px;
                        justify-content: space-between;
                        .item-thumb {
                            --size: 40px;
                            width: var(--size);
                            height: var(--size);
                            overflow: hidden;
                            border-radius: 50%;
                            img {
                                width: calc(var(--size) + 20px);
                            }
                        }
                    }
                    .actions {
                        min-width: 330px;
                    }
                    .quantity {
                        width: 100px;
                        justify-content: space-between;
                        .icon {
                            &-l {
                                width: 20px;
                                height: 20px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                cursor: pointer;
                                transition: all 0.2s ease;
                                &:hover {
                                    background-color: var(--primary-color-opacity);
                                }
                            }
                            &-r {
                                width: 20px;
                                height: 20px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                cursor: pointer;
                                transition: all 0.2s ease;
                                &:hover {
                                    background-color: var(--primary-color-opacity);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
