.btn {
    &.brWhite {
        background-color : var(--white) !important;
    }
    &.fSizelarge {
        font-size: 1.8rem;
    }
    &.fSizesmall {
        font-size: 1.2rem;
    }
    
    &.w100p {
        width: calc(100% - 4px * 2);
    }
    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 1.6rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;

    border: none;
    cursor: pointer;

    padding: 6px 8px;
    margin: 0 4px;

    min-width: 80px;
    border-radius: 4px;
    letter-spacing: 1px;

    transition: all 0.3s linear;
    border: 1px solid transparent;
    transition: all 0.2s;
    & .icon {
        --spacing: 6px;
        &-#{L} {
            margin-right: var(--spacing);
        }
        &-#{R} {
            margin-left: var(--spacing);
        }
        display: flex;
        align-items: center;
    }
    &.fw3 {
        font-weight: 300;
    }
    &.w12 {
        width: 12rem;
    }
    &.primary {
        background-color: var(--primary-color);
        color: var(--white);
        &:hover {
            background-color: var(--primary-color-lightly);
        }
    }
    &.outline {
        border-color: var(--primary-color);
        background-color: transparent;
        color: var(--primary-color);
        &:hover {
            background-color: var(--primary-color-opacity);
        }
        &.darkColor {
            border-color: var(--black);
            color: var(--black);
            &:hover {
                background-color: var(--gray);
            }
        }
    }
    &.white {
        &.darkTheme {
            background-color: var(--gray);
        }
        background-color: var(--white);
        box-shadow: 0 1px 1px 0 rgb(0 0 0 / 2%);
    }
    &.superSmall {
        font-size: 1.2rem;
        padding: 2px 4px;
    }
    &.small {
        padding: 3px 6px;
    }
    &.large {
        padding: 12px 16px;
    }
}
