:root {
    --gray-color: rgb(226 226 226);
    --white-color: #fff;
}

.wrapper {
    background-color: var(--white-color);
    width: 100%;
    max-height: 200px;
    border-radius: 8px;
    box-shadow: rgb(0 0 0 / 12%) 0 2px 12px;
    min-height: 100px;
    padding-top: 8px;
    overflow-y: scroll;
    &::-webkit-scrollbar {
        width: 0;
    }
    &::-webkit-scrollbar-thumb {
        background-color: var(--gray-color);
        border-radius: 10px;
    }
}
