@import 'normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,300&display=swap');

// vairable:
:root {
    --primary-color: #e7309b;
    --primary-color-gradient: linear-gradient(23deg, rgba(110, 197, 234, 0.7058823529), rgba(174, 147, 255, 0.7254901961), rgba(232, 49, 156, 0.8352941176));
    --primary-color-lightly: #e7309bb0;
    --primary-color-opacity: #e7309b54;
    --gray: rgb(216 216 218 / 65%);
    --gray-text: rgb(0 0 0 / 26%);
    --white: #fff;
    --black: #000;
    --black-opacity: rgba(0, 0, 0, 0.205);
    --black-opacity-galerry: rgba(0, 0, 0, 0.76);
    --br-color-list-item: rgb(245 245 245);

    --br-dark-theme: #092c3e;
    --br-dark-theme-2: #273942;
    
    // size
    --padding-section-detail-product: 10rem;
    --size-img-avatar: 60px;
    --margin-item: 4px;
    --width-search-box: 340px;
    --default-layout-header-height: 60px;
    --default-layout-content-width: 1150px;
    --sidebar-width-wrapper: 356px;
    --content-page-width-wrapper: calc(
        var(--default-layout-content-width) - var(--sidebar-width-wrapper)
    );
    --width-logo-header: 50px;
    --width-avatar-user-header: 45px;
    --height-search-box: 40px;

}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    scroll-behavior: smooth;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
}

a {
    text-decoration: none;
    color: #222;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

button,
input {
    outline: none;
    border: none;
}

::-webkit-scrollbar {
    width: 4px;
    height: 4px;
}
  
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 1px var(--white); 
    border-radius: 10px;
}
   
  /* Handle */
::-webkit-scrollbar-thumb {
    background: var(--gray); 
    border-radius: 10px;
}

  /* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background-color: var(--primary-color);
}