.wrapper {
    &::-webkit-scrollbar {
        display: none;
    }
    & {
        -ms-overflow-style: none; /* IE and Edge */
        scrollbar-width: none; /* Firefox */
    }
    max-width: 100%;
    overflow-x: scroll;
    display: flex;
    justify-content: center;
    &.small {
        transform: scale(.6);
    }
    .pagination {
        width: 100%;
        @media (max-width: 1160px) {
            justify-content: start;
        }
        display: flex;
        justify-content: center;
        align-items: center;
        .icon {
            --size: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: var(--size);
            height: var(--size);
        }
    }    
}
