.wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--black-opacity-galerry);
    &,
    .galerry-img-show {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    z-index: 9999;
    .galerry {
        position: relative;
        .galerry-img-show {
            min-width: var(--default-layout-content-width);
            width: 80vw;
            height: 80vh;
            overflow-y: hidden;
            img {
                width: 70vw;
                transition: .1s linear;
                border-radius: 8px;
                &:hover {
                    transform: scale(1.01);
                }
            }
        }
        .galerry-icon-left,
        .galerry-icon-right {
            &:hover {
                color: var(--white);
            }
            cursor: pointer;
            transition: .1s linear;
            position: absolute;
            color: var(--primary-color);
            font-size: 4rem;
            top: 50%;
            transform: translateY(-50%);
            --spacing-icon: 2rem;
            --size-icon: 2rem;
            width: var(--size-icon);
            height: var(--size-icon);
        }

        .galerry-icon-left {
            left: var(--spacing-icon);
        }
        .galerry-icon-right {
            right: var(--spacing-icon);
        }
        .galerry-dots {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            li {
                --size-dot: 12px;
                margin: 2px;
                width: var(--size-dot);
                height: var(--size-dot);
                background-color: var(--white);
                border-radius: 50px;
                &:hover {
                    transition: .3s linear;
                    background-color: var(--primary-color);
                }
                &.active {
                    background-color: var(--primary-color);
                    width:  calc(var(--size-dot) + 10px);
                } 
            }
        }
    }
}