.wrapper {
    --item-count-inline: 3;
    .header {
        background-color: var(--primary-color-opacity);
        text-align: center;
        font-weight: 500;
        font-size: 1.4rem;
        padding: 4px 8px;
        margin-bottom: 8px;
    }
    .content {
        overflow-y: scroll;
        width: 100%;
        display: flex;
        justify-content: start;
        align-items: center;
        .list-suggest-product {
            margin-left: 2rem;
            display: flex;
            justify-content: space-around;
            .item-suggest-product {
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;
                height: 140px;
                z-index: 0;
                margin: 0 1rem .8rem;
                width: calc(
                    100% / var(--item-count-inline, 3) - 2px * var(--item-count-inline, 3)
                );
                min-width: 20rem;
                &:hover {
                    z-index: 1;
                }
                a {
                    .wp-thumb {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        position: relative;
                        border: 1px solid transparent;
                        transition: all 0.2s linear;
                        overflow: hidden;
                        &:hover {
                            border-color: var(--primary-color);
                            transform: scale(1.1);
                        }
                        &:hover .wp-info {
                            transform: translateY(-30px) translateX(-50%) scale(1.1);
                        }
                        img {
                            width: 100%;
                        }
                    }
                    .wp-info {
                        p {
                            font-size: 1.2rem;
                        }
                        position: absolute;
                        left: 50%;
                        bottom: -20px;
                        transform: translateX(-50%);
                        width: 100%;
                        text-align: center;
                        transition: all 0.2s linear;
                        background-color: var(--gray);
                    }
                }
            }
        }
    }
}
