@keyframes randomColorBorder {
    15% {
        border-color: rgb(219, 18, 18);
    }
    30% {
        border-color: rgb(241, 169, 14);
    }
    45% {
        border-color: rgb(84, 238, 23);
    }
    60% {
        border-color: rgb(30, 214, 205);
    }
    75% {
        border-color: rgb(34, 110, 197);
    }
    90% {
        border-color: rgb(151, 35, 197);
    }
    100% {
        border-color: rgb(214, 66, 147);
    }
}

@keyframes randomColor {
    0% {
        color: rgb(196, 52, 52);
    }
    30% {
        color: rgb(180, 142, 61);
    }
    45% {
        color: rgb(111, 201, 76);
    }
    60% {
        color: rgb(67, 201, 194);
    }
    75% {
        color: rgb(67, 118, 177);
    }
    90% {
        color: rgb(149, 71, 180);
    }
    100% {
        color: rgb(189, 75, 138);
    }
}
