@import '../../GlobalStyles/keyframe.scss';
.wrapper {
    width: 100%;
    margin: 2rem 0;
    .info-user {
        display: flex;
        align-items: center;
        .avatar {
            --size: 40px;
            height: var(--size);
            width: var(--size);
            
            display: flex;
            justify-content: center;
            align-items: center;

            border-radius: 50%;
            border: 1px solid;
            animation: randomColorBorder 20s infinite;
            
            overflow: hidden;
            img {
                width: var(--size-img-avatar);
            }
        }
        .name {
            margin: 4px;
            h4 {
                padding: 4px 8px;
                font-size: 1.4rem;
            }
        }

    }   
    .content {
        margin-left: 52px;
        font-size: 1.3rem;
        .img,
        .video {
            margin-top: 1.6rem;
            margin-bottom: 0.4rem;
            .img-feedback,
            .video-feedback 
            {
                font-size: 1.4rem;
            }
            .list-img,
            .list-video
             {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                --margin-size-item: .4rem;
                --item-count-inline: 2;
                .item-video {
                    width: calc(100% / var(--item-count-inline) - var(--margin-size-item) * 2 * var(--item-count-inline));
                }
                .item-img {
                    width: 100px;
                }
                .item-img,
                .item-video
                 {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: var(--margin-size-item);
                    overflow: hidden;
                    img
                     {
                        width: 110px;
                    }
                    video {
                        width: 100%;
                    }
                }
            }
        }
        
        .create-at {
            margin: 1.6rem 0;
        }
        .options {
            margin-left: auto;
            .icon-three-dots {
                font-size: 2rem;
                width: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                margin-left: auto;
                
            }
            .list-action {
                display: flex;
                flex-direction: column;
                .item-action {
                    cursor: pointer;
                    &:hover {
                        background-color: var(--gray);
                    }
                    transition: .2s linear;
                    padding: 4px;
                    display: flex;
                    align-items: center;
                    justify-content: start;
                    .icon {
                        width: 30px;
                        height: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                    .content-option {
                        height: 100%;
                        flex: 1;
                        width: calc(100% - 30px);
                        h4 {
                            text-align: left;
                            width: 100%;
                            padding-right: 8px;
                        }
                    }
                }
            }
        }
    }
}