.wrapper {
    padding-top: 3.2rem;
    .header {
        max-width: 99vw;
        .list-title-tab {
            display: flex;
            justify-content: space-around;
            .title-tab {
                transition: cubic-bezier(0.6, -0.28, 0.735, 0.045) .2s;
                text-transform: uppercase;
                font-weight: 500;
                text-align: center;
                font-size: 1.4rem;
                cursor: pointer;
                padding: 6px 16px;
                border-bottom: 2px solid var(--gray);
                &:hover {
                    border-color: var(--primary-color);
                    background-color: var(--primary-color-opacity);
                    transform: translateY(-2px);
                }
                &.active {
                    border-color: var(--primary-color);
                    background-color: var(--primary-color-opacity);
                    transform: translateY(-2px);
                }
            }
        }
    }
    .content {
        margin-top: 1.6rem;
        min-height: 20rem;
        .text {
            font-size: 1.4rem;
        }
    }
}