@import '../../../GlobalStyles/keyframe.scss';

.wrapper {
    z-index: 99;
    top: 0;
    left: 0;
    right: 0;
    position: fixed;
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%);
    background-color: var(--white-color);
    display: flex;
    justify-content: center;
    align-items: center;
    height: var(--default-layout-header-height, 60px);
    width: 100%;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.4);
    transition: all 0.3s linear;
    .inner {
        margin: 0 2rem;
        width: var(--default-layout-content-width, 1150px);
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .logo {
            user-select: none;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            .logo-link {
                display: flex;
                justify-content: center;
                align-items: center;
                img {
                    transition: all 0.3s linear;
                    width: 150px;
                }
            }
        }
        .logo-text {
            text-align: center;
            font-size: 1.6rem;
        }
        .search-box {
            @media (max-width: 1160px) {
                position: fixed;
                top: calc(var(--default-layout-header-height, 60px) + 1rem);
                &.openMb {
                    right: 1rem;
                }
                right: calc(-1 * var(--width-search-box));
            }
            transition: all .2s linear;
            width: var(--width-search-box);
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: var(--height-search-box);
            background-color: var(--gray);
            border-radius: 24px;
            overflow: hidden;
            input {
                flex: 1;
                font-size: 1.4rem;
                background-color: transparent;
                height: 100%;
                width: 300px;
                padding-left: 14px;
                transition: all 0.2s;
            }
            button {
                position: relative;
                height: 100%;
                border: none;
                padding: 8px 12px;
                cursor: pointer;
                background-color: transparent;
                &:hover {
                    background-color: rgb(234 234 235);
                }
                &::after {
                    position: absolute;
                    content: '';
                    width: 1px;
                    height: calc(100% - 14px);
                    background-color: red;
                    left: 0;
                    top: 7px;
                    background: rgba(22, 24, 35, 0.12);
                }
            }
        }
        .search-result {
            width: var(--width-search-box);
            .ls-result {
                li {
                    .item-result {
                        display: flex;
                        align-items: center;
                        padding: 9px 16px;
                        width: 100%;
                        transition: all 0.3s linear;
                        &#{_icon} {
                            --size: 20px;
                            width: var(--size);
                            height: var(--size);

                            display: flex;
                            align-items: center;
                            justify-content: center;

                            font-size: 12px;
                            margin-right: 8px;
                        }
                        &#{_content} {
                            display: flex;
                            align-items: center;
                            img {
                                border-radius: 50%;
                                --size: 30px;
                                width: var(--size);
                                height: var(--size);
                            }
                            h3 {
                                white-space: nowrap;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                width: calc(var(--width-search-box) - 100px);
                                margin-left: 8px;
                                font-size: 1.6rem;
                                line-height: 2.2rem;
                            }
                        }
                    }
                    transition: 0.1s;
                    &:hover {
                        background-color: var(--gray);
                    }
                }
            }
        }
        .actions {
            display: flex;
            align-items: center;
            .avatarUser {
                user-select: none;
                width: var(--width-avatar-user-header);
                height: var(--width-avatar-user-header);
                .link {
                    border-bottom: 2px solid;
                    animation: randomColorBorder 20s infinite;
                    height: 100%;
                    border-radius: 50%;
                    overflow: hidden;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    img {
                        width: var(--size-img-avatar);
                    }
                }
            }
            .three-dots {
                margin-left: 8px;
                padding: 4px;
                font-size: 2.5rem;

                display: flex;
                align-items: center;
                cursor: pointer;
                transition: all 0.1s;
            }
            .option-lang .item-more-action {
                justify-content: end;
                min-width: 135px;
            }
            .list-more-action {
                .item-more-action {
                    &:hover {
                        background-color: var(--gray);
                    }
                    cursor: pointer;
                    padding: 10px 16px;
                    display: flex;
                    align-items: center;
                    .content {
                    }
                    .icon {
                        margin-right: 8px;
                        display: flex;
                        align-items: center;
                    }
                }
            }
        }
    }
}
