.wrapper {
    position: relative;
    overflow: hidden;
    background: var(--primary-color-gradient);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .button-backHome {
        position: absolute;
        top: 2rem;
        left: 2rem;
    }
    .form {
        border-radius: 4px;
        padding: 24px;
        width: 400px;
        background-color: var(--white);
        .header {
            color: var(--primary-color);
            margin-bottom: 8px;
            font-size: 1.8rem;
        }
        .form-group {
            position: relative;
            margin-bottom: 8px;
            input {
                font-size: 1.4rem;
                height: 40px;
                line-height: 22px;
                padding: 8px 0;
                width: 100%;
                &::placeholder {
                    font-size: 1.4rem;
                }
            }
            &::after {
                position: absolute;
                content: "";
                background: var(--primary-color-gradient);
                width: 100%;
                height: 1px;
                bottom: 0;
                left: 0;
            }
        }
        .wrapper-btn {
            padding: 8px 0;
            display: flex;
            align-items: center;
            margin: 16px 0 16px -4px;
            p {
                margin-left: 4px;
            }
        }
        .promise {
            p {
                text-align: center;
                font-size: 1.2rem;
                color: var(--black);
            }
        }
    }

    .box-ballon {
        position: absolute;
        opacity: 0.3;
        pointer-events: none;
        user-select: none;
        animation: box-ballon_1 12s linear infinite;
    }
    .box-ballon:nth-child(1) {
        width: 60px;
        height: 40px;
        animation-delay: .2s;
        left: 5%;
        opacity: 0.3;
    }
    .box-ballon:nth-child(2) {
        width: 30px;
        opacity: 0.3;
        height: 30px;
        border-radius: 50%;
        left: 11%;
        animation-delay: 1s;
    }
    .box-ballon:nth-child(3) {
        width: 70px;
        opacity: 0.3;
        height: 30px;
        left: 40%;
        animation-delay: 2s;
    }
    .box-ballon:nth-child(4) {
        left: 70%;
        opacity: 0.3;
        width: 40px;
        height: 60px;
        border-radius: 10px;
        animation-delay: 3s;
    }
    .box-ballon:nth-child(5) {
        left: 90%;
        opacity: 0.3;
        width: 50px;
        height: 50px;
        animation-delay: 4s;
    }
    .box-ballon:nth-child(6) {
        left: 61%;
        opacity: 0.3;
        width: 70px;
        height: 30px;
        animation-delay: 5s;
    }
    .box-ballon:nth-child(7) {
        left: 20%;
        opacity: 0.3;
        width: 40px;
        height: 60px;
        border-radius: 10px;
        animation-delay: 6s;
    }
    .box-ballon:nth-child(8) {
        left: 83%;
        opacity: 0.3;
        width: 50px;
        height: 50px;
        animation-delay: 7s;
    }
    .lightHight {
        margin: 4px;
        color: var(--primary-color);
        font-weight: 500;
    }
}


@keyframes box-ballon_1 {
    0% {
        background-color:#BA2EFF;
        bottom: -10%;
        transform: rotate(0deg) translateX(0);
    }
    25% {
        background-color: #8F85ED;
        transform: rotate(90deg) translateX(-100px);
    } 
    50% {
        background-color:#79B6FF;
        transform: rotate(180deg)
    }
    75% {
        background-color:#61E8E8;
        transform: rotate(270deg) translateX(100px);
    }
    100% {
        background-color:#6BFFA8;
        bottom: 110%;
        transform: rotate(360deg) translateX(0);
    }
}