@import '../../GlobalStyles/keyframe.scss';
.wrapper {
    
    .content-send-btn,
    .avatar,
    .content {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    width: 100%;
    margin: 2rem 0;
    
    .info-user {
        display: flex;
        align-items: center;
        .avatar {
            --size: 40px;
            height: var(--size);
            width: var(--size);
            

            border-radius: 50%;
            border: 1px solid;
            animation: randomColorBorder 20s infinite;
            
            overflow: hidden;
            img {
                width: var(--size-img-avatar);
            }
        }
        .name {
            margin: 4px;
            h4 {
                padding: 4px 8px;
                font-size: 1.4rem;
            }
        }

    }   
    .content {
        --size-btn-send: 32px;
        margin-left: 52px;
        font-size: 1.3rem;
        position: relative;
        input {
            height: 32px;
            width: calc(100% - var(--size-btn-send));
            background-color: var(--gray);
            border-radius: 25px;
            text-align: center;
        }
        .content-send-btn {
            width: var(--size-btn-send);
            height: var(--size-btn-send);
            cursor: pointer;
            transition: all 0.2s ease-in-out;
            &:hover {
                font-size: 2rem;
                color: var(--primary-color);
            } 
        }
    }
}