.wrapper {
    background-color: var(--white-color);
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    position: fixed;
    z-index: 98;
    top: calc(var(--default-layout-header-height, 60px) + 3px);
    overscroll-behavior-y: contain;
    width: var(--sidebar-width-wrapper, 356px);
    max-width: 100vw;
    padding-bottom: 10rem;
    transition: all 0.1s linear;
    left: calc((100vw - 1150px) / 2);
    @media (max-width: 1160px) {
        &.openMb {
            left: 0;
        }
        left: calc(-1 * var(--sidebar-width-wrapper, 356px));
    }
}
