.wrapper {
    @media (max-width: 576px) {
        width: 170px;
    }
    position: relative;
    z-index: 1;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
    transition: .2s ease;
    &:hover {
        box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
        transform: translateY(-2px);
    }

    --item-count-inline: 3;
    background-color: var(--white);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: calc(
        (
                var(--default-layout-content-width, 1150px) -
                    var(--sidebar-width-wrapper, 356px)
            ) / var(--item-count-inline) - var(--margin-item) * 2 *
            var(--item-count-inline)
    );
    padding: 0 4px;
    margin: var(--margin-item);
    margin-bottom: 16px;
    .thumbnail {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        &,
        img {
            width: 100%;
        }
    }
    .bottom-content {
        padding: 8px;
        .title {
            word-wrap: break-word;
            white-space: normal;
            overflow: hidden;
            display: -webkit-box;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;

            line-height: 14px;
            font-size: 1.2rem;
            font-weight: 600;
            padding: 4px 0;
            margin-bottom: 4px;
        }
        .content {
            display: flex;
            align-items: center;
            padding: 4px 0;
            .icon {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-right: 4px;
            }
        }
        .actions {
            padding: 4px 0;
            margin: 0 -4px;
            width: 100%;
            align-items: center;
            justify-content: space-between;
            display: flex;
            flex-wrap: wrap;
            @media (max-width: 576px) {
                height: 70px;
            }
        }
    }

    .option {
        &,
        .icon {
            display: flex;
            align-items: center;
        }
        &.favorite {
            color: var(--white);
            background-color: var(--primary-color);
        }
        &.trending {
            color: var(--white);
            background-color: rgb(228 138 60);
        }
        &.newest {
            color: var(--white);
            background-color: rgb(223 50 39);
        }
        border-bottom-left-radius: 8px;
        right: 0;
        padding: 4px 6px;
        position: absolute;
        font-size: 1.0rem;
        &:hover .icon {
            transform: scale(1.3);
        }
        .icon {
            transition: .3s ease-in-out;
            margin-right: 4px;
        } 
    }
}
