.wrapper {
    @media (max-width: 1160px) {
        position: fixed;
        z-index: 2;
        flex-direction: column;
        height: 250px;
        width: 18rem;
        background-color: var(--gray);
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
        top: 20%;
        &::-webkit-scrollbar {
            display: none;
        }
        & {
            -ms-overflow-style: none; /* IE and Edge */
            scrollbar-width: none; /* Firefox */
        }
        &.dark-mode-mB {
            background-color: var(--br-dark-theme-2);
        }
    }
    transition: .2s linear;
    border-radius: 4px;
    display: flex;
    padding: 13px 20px;
    align-items: center;
    background-color: var(--primary-color-opacity);
    max-width: 100%;
    overflow-y: scroll;
    width: 100%;
    .title {
        margin-left: 8px;
        margin-right: auto;
        @media (max-width: 1160px) {
            width: 100%;
            text-align: center;
        }
    }
    .optionsBar {
        @media (max-width: 1160px) {
            flex-direction: column;
            align-items: end;
            height: 140px;
        }
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin-left: 1rem;
        flex: 1;
        .list-option {
            padding: 10px 0;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            .item-option {
                padding: 4px 16px;
                font-size: 1.4rem;
                transition: 0.2s linear;
                &:hover {
                    background-color: var(--gray);
                }
                cursor: pointer;
            }
        }
    }
}
