.wrapper {
    @media (max-width: 874px) {
        width: 384px;
    }
    max-width: 80vw;
    max-height: 80vh;
    background-color: var(--white);
    border-radius: 4px;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 13%);
    padding: 3rem;
    form {
        .header {
            .icon,
            & {
                display: flex;
                justify-content: center;
                align-items: center;
            }
            .icon {
                margin-right: 4px;
            }
            h3 {
            }

        }
        .wp-section {
            max-height: 371px;
            overflow-y: auto;
            margin-top: 1rem;
            .section {
                padding: 0 1rem
            }
            display: flex;
            justify-content: center;
            align-items: start;
            flex-wrap: wrap;
        }
        .form-group {
            position: relative;
            font-size: 1.2rem;
            display: flex;
            flex-direction: column;
            align-items: start;
            width: 30rem;
            label {
                font-size: 1.4rem;

            }
            select,
            input {
                width: 100%;
                border: 1px solid;
                border-color: var(--gray);
                height: 32px;
            }
            select {
                outline: none;
            }
            .avatar {
                background-color: var(--white);
                border: 1px solid var(--gray);
                padding: 2px 12px;
                margin-bottom: 2rem;
                cursor: pointer;
                input {
                    display: none;
                }
            }
            textarea {
                min-height: 10rem;
                border-color: var(--gray);
                width: 100%;
                outline: none;
            }
            input,
            textarea {
                padding: .8rem;
            }
            .ms {
                height: 2rem;
                color: red;
                margin-bottom: .8rem;
            }
            margin-bottom: 2rem;
            &::after {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0;
                height: 1px;
                width: 100%;
                background: var(--primary-color-gradient);
            }
        }
    }
}