.wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    .main-thumbnail-wrapper {
        cursor: pointer;
        user-select: none;
        display: flex;
        justify-content: center;
        align-items: center;
        .main-thumbnail {
            border: 2px solid var(--gray);
            padding: 5rem;
            overflow: hidden;
            height: 480px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                transition: all 0.7s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                &:hover {
                    transform: scale(1.1) rotate(2deg);
                }
                width: 100%;
                max-height: 370px;
            }

        }
    }
    .main-thumbnail-bottom {
        .more-option {
            user-select: none;
            text-align: center;
            font-weight: 400;
            text-transform: uppercase;
            font-size: 1.2rem;
            padding: 8px 0;
            width: 100%;
        }
        .option-wrapper {
            display: flex;
            flex-wrap: wrap;
            width: 50%;
            margin: 0 auto;
            justify-content: center;
            .option {
                margin: 2px;
                user-select: none;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                width: calc(100% / 5);
                border: 2px solid var(--gray);
                &.active {
                    border-color: var(--primary-color);
                }
                img {
                    width: 100%;
                }
            }
        }
    }
}