.wrapper {
    width: 100%;
    .list-navigation {
        .item-navigation,
        .icon {
            display: flex;
            align-items: center;
        }
        .item-navigation {
            padding: 8px;
            transition: all 0.3s linear;
            &.active {
                color: var(--primary-color) !important;
            }
            .icon {
                --size: 32px;
                width: var(--size);
                height: var(--size);
                justify-content: center;
                font-size: 2.4rem;
            }
            .content {
                font-size: 1.8rem;
                line-height: 25px;
                margin-left: 8px;
            }
        }
    }
}
