.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    .container {
        display: flex;
        margin-top: var(--default-layout-header-height, 60px);
        padding-top: 20px;
        max-width: 100%;
        // justify-content: space-between;
        width: var(--default-layout-content-width, 1150px);
        .content {
            margin-left: var(--sidebar-width-wrapper, 356px);
            flex: 1;
            width: var(--content-page-width-wrapper);
            max-width: 100%;
            @media (max-width: 1160px) {
                margin-left: 0;
            }
        }
    }
}
