.wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 16px;
    &.start {
        justify-content: start;
    }
    .title {
        
        &,
        .icon {
            display: flex;
            align-items: center;
        }
        
        .icon {
            --side: 32px;
            width: var(--side);
            height: var(--side);
            font-size: 2rem;
            justify-content: center;
        }
        
        .content {
            font-size: 14px;
            line-height: 20px;
            font-weight: 300;
        }
    }

    .list-suggest {
        margin: 8px 0;
        li {
            transition: .2s linear;
            border-radius: 4px;
            padding: 8px;
            &:hover {
                transform: translateX(2px);
                background-color: var(--gray);
            } 
            .item-suggest {
                display: flex;
                align-items: center;
                .avatar {
                    --side: 35px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    img {
                        width: var(--side-img-avatar);
                        border-radius: 50%;
                    }
                }
                .content {
                    padding-left: 8px;
                    --limit-width: calc(var(--sidebar-width-wrapper, 356px) - 51px);
                    .title,
                    .description {
                        max-width: var(--limit-width);
                        
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;

                    }
                    
                    .title-content {
                        font-weight: 600;
                        font-size: 1.6rem;
                        line-height: 2.2rem;
                    }
                    .description {
                        font-weight: 400;
                        font-size: 1.2rem;
                        line-height: 1.5rem;
                    }
                }
            }
        }
    }
}
