.wrapper {
    border-radius: 8px;
    background-color: #fff;
    padding: 16px;
    font-size: 1.4rem;
    width: 1000px;
    max-width: calc(100vw - 32px);
    max-height: calc(80vh);
    overflow-y: scroll;
    .heading {
        text-align: left;
        width: 100%;
        padding: 8px 0;
        font-size: 2.2rem;
        font-weight: 600;
        background: var(--primary-color);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    .order {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        .info-product {
            .title {
                font-size: 1.4rem;
                text-align: center;
                padding: 4px 0;
            }
            &.txt {
                @media (max-width: 675px) {
                    margin: 0 auto;
                    width: calc(100vw - 32px);
                }
                border-left: 1px solid var(--primary-color-opacity);
                border-right: 1px solid var(--primary-color-opacity);
                border-radius: 8px;
                padding: 16px;
                padding-top: 0;
                --size: 30px;
                .quantity-input {
                    background-color: transparent;
                }
                .quantity-input,
                .icon {
                    width: var(--size);
                    height: var(--size);
                }
                .icon {
                    &:hover {
                        background-color: var(--primary-color);
                    }
                    transition: 0.3s linear;
                    background: var(--primary-color-opacity);
                    cursor: pointer;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 4px;
                }

                .icon:nth-child(1) {
                    margin-left: 6px;
                }

                margin-right: 8px;
                width: 40%;
                min-width: 300px;
                .content {
                    .section {
                        position: relative;
                        span {
                            font-weight: 500;
                        }
                        padding: 4px 0;
                        margin: 4px 0;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;
                        margin-bottom: 2rem;
                        &::after {
                            content: '';
                            width: 100%;
                            height: 1px;
                            left: 0;
                            bottom: 0;
                            position: absolute;
                            background: var(--primary-color-gradient);
                        }
                    }
                }
                .action-controll {
                    width: 100%;
                    display: flex;
                    justify-content: start;
                    align-items: start;
                    flex-direction: column;
                    .action {
                        padding: 4px 0;
                        margin-top: 1rem;
                        &.note {
                            margin-top: 0;
                            font-size: 1.2rem;
                        }
                        &-quantity {
                            align-items: center;
                            display: flex;
                            .quantity-input {
                                display: block;
                                text-align: center;
                            }
                        }
                        &.totalPrice {
                            border: 1px solid var(--gray);
                            padding: 4px 8px;
                            margin: 2rem 0;
                        }
                    }
                }
                .btn {
                    padding: 4px 0;
                    margin: 4px 0 4px -4px;
                }
                .option {
                    .suggest {
                        a {
                            color: var(--primary-color);
                        }
                    }
                }
            }
            &.img {
                flex: 1;
                min-width: 300px;
                .item-thumb,
                .list-thumb-product {
                    display: flex;
                    align-items: center;
                }
                .list-thumb-product {
                    margin-bottom: 1rem;
                    max-height: 400px;
                    overflow-y: scroll;
                    overflow-x: hidden;
                    width: 100%;
                    justify-content: space-around;
                    flex-wrap: wrap;
                    .item-thumb {
                        transition: all 0.2s linear;
                        &:hover {
                            transform: rotate(-5deg);
                        }
                        justify-content: center;
                        width: 50%;
                        padding: 0 16px;
                        margin: 4px 0;
                        overflow: hidden;
                        border-radius: 4px;
                        img {
                            width: calc(100% + 20px);
                        }
                    }
                }
            }
        }
    }
}
