.wrapper {
    background-color: var(--black-opacity-galerry);
    &.transparent {
        background-color: transparent;
    }
    &.lightly {
        background-color: #00000036;
    }
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10000;
    display: flex;
    justify-content: center;
    align-items: center;    
    .modal-popup {
        min-width: 300px;
        min-height: 400px;
    }
}
