.wrapper {
    position: fixed;
    bottom: 5rem;
    right: 1rem;
    z-index: 1;
    @media (max-width: 1160px) {
        display: block;
    }
    display: none;
    ul {
        transition: .2s linear;
        width: 10rem;
        &.show {
            max-height: calc(50px * 3);
        }
        display: flex;
        justify-content: end;
        align-items: center;
        flex-direction: column;
        max-height: 45px;
        overflow: hidden;
        li {
            width: 100%;
            margin: 6px 0;
            border-radius: 4px;
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
        }
    }
}
