@import '../../components/GlobalStyles/keyframe.scss';

.wrapper-use-page {
    padding-top: 40px;
    min-height: calc(100vh - var(--default-layout-header-height, 60px));
    margin: 0 1rem;
    width: var(--default-layout-content-width, 1150px);
    max-width: 100vw;
    margin-top: var(--default-layout-header-height, 60px);
    .thumbnail-user {
        position: relative;
        height: 90px;
        .icon-crown {
            position: absolute;
            font-size: 1.6em;
            top: -20px;
            left: 32px;
            top: -24px;
            color: var(--primary-color);
        }
        justify-content: start;

        &,
        .avatar,
        .icon {
            display: flex;
            align-items: center;
        }

        .name {
            height: 100%;
            margin: 0 3.2rem;
            line-height: 32px;
            font-size: 1.8rem;
            display: flex;
            justify-content: space-evenly;
            align-items: start;
            flex-direction: column;
            h2 {
                margin-left: 4px;
            }
        }

        .avatar {
            background-color: var(--white);
            z-index: 1;
            position: relative;
            justify-content: center;
            overflow: hidden;
            width: 90px;
            height: 90px;
            border-radius: 50%;
            border: 2px solid;
            animation: randomColorBorder 20s infinite;
            img {
                width: calc(100% + 20px);
            }
        }

        .name {
        }

        .icon {
            justify-content: center;
        }
    }
    .list-action {
        max-height: 100px;
        li {
            .icon,
            & {
                transition: all 0.2s linear;
            }
            padding: 8px 16px;
            &:hover {
                background-color: var(--gray);
            }
            &:hover .icon {
                transform: scale(1.3);
            }
        }
    }
}
