.wp {
    padding: 2rem;
    font-size: 1.4rem;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 13%);
    .content {
        display: flex;
        max-width: 99vw;
        justify-content: start;
        ul {
                &:nth-child(1) {
                    max-width: 25%;
                }
                &:nth-child(2) {
                    max-width: 65%;
                    padding-left: 1rem;
                }
                .item {
                    margin-right: auto;
                }
            }
            ul li {
                padding: 4px;
                max-width: 100%;
                margin-bottom: 1rem;
                min-height: 40px;
                word-wrap: break-word;
            }
            &.left {
                padding-right: 2rem;
            }
            &.left ul {
                display: flex;
                flex-direction: column;
                align-items: end;
                padding: 0 4px;
            }
    }
}